"use client";

import {
    collection,
    DocumentData,
    onSnapshot,
    orderBy,
    query,
} from "firebase/firestore";
import React, { useEffect, useState } from "react";
import { AiOutlineClockCircle } from "react-icons/ai";
import { RxEnvelopeOpen } from "react-icons/rx";
import { db, sendReservation } from "src/libs/firebase";
import { RevealWrapper } from "next-reveal";

function Reservation() {
    const [inputValue, setInputValue] = useState<{
        name: string;
        presence: string;
        accInvitees: string;
        guests: string;
        message: string;
    }>({
        name: "",
        presence: "Attending",
        accInvitees: "",
        guests: "",
        message: "",
    });
    const [reservations, setReservations] = useState<DocumentData[]>([]);

    useEffect(() => {
        const q = query(collection(db, "reservation"), orderBy("time", "desc"));

        return onSnapshot(q, (querySnapshot) => {
            setReservations(
                querySnapshot.docs.map((doc) => {
                    return { id: doc.id, ...doc.data() };
                })
            );
        });
    }, []);

    function getZero(num: number) {
        return num < 10 ? `0${num}` : `${num}`;
    }

    function handleSubmit() {
        sendReservation(
            inputValue.name,
            inputValue.presence,
            inputValue.accInvitees,
            inputValue.guests,
            inputValue.message
        );
        setInputValue({
            name: "",
            presence: "Attending",
            accInvitees: "",
            guests: "",
            message: "",
        });
    }

    return (
        <section id="reservation">
            <div className="bg-[url('/public/images/bg.png')] bg-center bg-no-repeat bg-cover px-8 py-16 lg:px-20 space-y-12 -mt-4">
                <RevealWrapper duration={2500} origin="bottom" className="space-y-8">
                    <h1 className="text-3xl italic font-semibold text-center text-[#333333]">
                        RSVP
                    </h1>
                    <p className="text-center text-base text-[#5A5A5A]">
                        We would love to have you celebrate with us. Please let us know if you can make it.
                    </p>
                    <div className="bg-white shadow-lg rounded-lg p-6 space-y-6 max-w-lg mx-auto">
                        <input
                            type="text"
                            placeholder="Name"
                            className="text-base py-3 px-4 placeholder:text-[#A9A9A9] border border-[#A99D87] rounded-lg focus:outline-none focus:ring-2 focus:ring-[#A99D87]"
                            value={inputValue.name}
                            onChange={(e) =>
                                setInputValue({ ...inputValue, name: e.target.value })
                            }
                        />
                        <textarea
                            placeholder="You can leave a message here"
                            className="h-28 text-base py-3 px-4 placeholder:text-[#A9A9A9] border border-[#A99D87] rounded-lg focus:outline-none focus:ring-2 focus:ring-[#A99D87]"
                            value={inputValue.message}
                            onChange={(e) =>
                                setInputValue({ ...inputValue, message: e.target.value })
                            }
                        />
                        <div className="flex items-center gap-4 text-sm">
                            <input
                                type="radio"
                                name="presence"
                                id="attending"
                                value="Attending"
                                checked={inputValue.presence === "Attending"}
                                onChange={(e) =>
                                    setInputValue({
                                        ...inputValue,
                                        presence: e.target.value,
                                    })
                                }
                            />
                            <label htmlFor="attending" className="text-[#5A5A5A]">
                                Attending
                            </label>
                            <input
                                type="radio"
                                name="presence"
                                id="not-attending"
                                value="Not Attending"
                                checked={inputValue.presence === "Not Attending"}
                                onChange={(e) =>
                                    setInputValue({
                                        ...inputValue,
                                        presence: e.target.value,
                                        accInvitees: "",
                                        guests: "",
                                        message: "",
                                    })
                                }
                            />
                            <label htmlFor="not-attending" className="text-[#5A5A5A]">
                                Not Attending
                            </label>
                        </div>
                        {inputValue.presence === "Attending" && (
                            <div className="space-y-4">
                                <input
                                    type="text"
                                    placeholder="Names of Accompanying Invitees"
                                    className="text-base py-3 px-4 placeholder:text-[#A9A9A9] border border-[#A99D87] rounded-lg focus:outline-none focus:ring-2 focus:ring-[#A99D87]"
                                    value={inputValue.accInvitees || ""}
                                    onChange={(e) =>
                                        setInputValue({
                                            ...inputValue,
                                            accInvitees: e.target.value,
                                        })
                                    }
                                />
                                <input
                                    type="text"
                                    placeholder="Number of Guests"
                                    className="text-base py-3 px-4 placeholder:text-[#A9A9A9] border border-[#A99D87] rounded-lg focus:outline-none focus:ring-2 focus:ring-[#A99D87]"
                                    value={inputValue.guests || ""}
                                    onChange={(e) =>
                                        setInputValue({ ...inputValue, guests: e.target.value })
                                    }
                                />
                            </div>
                        )}
                        <button
                            onClick={handleSubmit}
                            className="bg-[#A99D87] flex items-center justify-center w-full rounded-lg text-white text-lg font-semibold py-3 px-4 hover:scale-105 transition-transform duration-200"
                        >
                            <RxEnvelopeOpen />
                            <span className="ml-2">Send RSVP</span>
                        </button>
                    </div>
                </RevealWrapper>
                <div className="space-y-6 max-w-lg mx-auto">
                    {reservations.map((data) => {
                        const date = new Date(data.time.seconds * 1000);
                        return (
                            <div
                                key={data.id}
                                className="bg-white shadow-lg p-4 border border-[#A99D87] rounded-lg text-[#424242] space-y-2"
                            >
                                <p className="flex items-center gap-2 text-lg font-semibold">
                                    <span>{data.name}</span>
                                    <span
                                        className={`text-sm ${
                                            data.presence === "Attending"
                                                ? "text-green-600"
                                                : "text-red-600"
                                        }`}
                                    >
                                        ({data.presence})
                                    </span>
                                </p>
                                <p className="text-base">{data.message}</p>
                                <p className="flex items-center gap-1 text-sm">
                                    <AiOutlineClockCircle />
                                    <span>
                                        {getZero(date.getDate())}/{getZero(date.getMonth() + 1)}/
                                        {getZero(date.getFullYear())}
                                    </span>
                                    <span className="ml-2">
                                        {getZero(date.getHours())}:{getZero(date.getMinutes())}:
                                        {getZero(date.getSeconds())}
                                    </span>
                                </p>
                            </div>
                        );
                    })}
                </div>
            </div>
        </section>
    );
}

export default Reservation;
